<template>
  <div>
    <audio
      :src="currentItem.file"
      ref="audio"
    ></audio>
  </div>
</template>
<script>
import {mapGetters, mapState} from 'vuex'
export default {
  computed: {
    audio () {
      return this.$refs.audio
    },
    ...mapState('player', ['pasued']),
    ...mapGetters('list', ['currentItem'])
  },
  mounted () {
    this.audio.oncanplay = () => {
      if (!this.pasued) {
        this.audio.play()
      }
    }
  }
}
</script>



